
.indexcounttile_wrap{padding-top: 0.15rem;background:#fff;position: relative;
}
.indexcounttile_wrap .morething{position:absolute;right:0.1rem;top:0.15rem;font-size: 0.12rem;text-decoration: none;display: block;height:0.2rem;line-height: 0.2rem;color:#ccc
}
.indexcounttile_wrap .adv{position:absolute;right:0.1rem;top:0.10rem;font-size: 0.12rem;text-decoration: none;display: block;height:0.2rem;padding:0 0.1rem;line-height: 0.2rem;color:#ccc;border:1px solid #ccc;border-radius: 0.06rem;
}
.indexcounttile_wrap .img{width:0.2rem;border-radius:50%;position: absolute;left:0.2rem;
}
.indexcounttile_wrap h3{color:#E88B5D;font-size: 0.18rem;height:0.2rem;line-height: 0.2rem;border-left: 0.08rem solid #E88B5D;text-indent: 1em;
}




