
.pictext_wrap {padding:0.17rem 0.2rem;border-bottom: 0.01rem solid #E4E4E4;background:#fff;position: relative;
}
.pictext_wrap  .text{float:left;width:70%
}
.pictext_wrap  .pic{float:left;width:30%;height:1rem
}
.pictext_wrap  .text h4{line-height: 0.25rem;color:#434343;font-size: 0.16rem;padding-bottom: 0.07rem;
}
.pictext_wrap  .text p{width:100%;height:0.52rem;line-height:0.18rem;color:#ababab;font-size: 0.14rem;text-overflow: ellipsis;overflow: hidden;display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:3;
}
.pictext_wrap  .text span{height:0.18rem;line-height: 0.18rem;color:#ababab;font-size: 0.14rem;display: block;
}
.pictext_wrap  .last{height:0.18rem;line-height: 0.18rem;color:#ababab;font-size: 0.14rem;position: absolute;right:0.2rem;bottom: 0.17rem;
}
.pictext_wrap  .del{height:0.18rem;line-height: 0.18rem;color:#ababab;font-size: 0.14rem;position: absolute;right:0.2rem;bottom: 0.17rem;
}



