
.user_wrap{background:#F59D54;padding:0.2rem 0 0 0.2rem
}
.user_wrap .textpic .userpic{float:left;margin-right: 0.15rem;width:0.6rem;border-radius: 50%;
}
.user_wrap .textpic .text{width:78%;float:left;border-bottom: 0.01rem solid #FDB46B;padding-bottom: 0.08rem;
}
.user_wrap .textpic .text h4{height:0.25rem;line-height: 0.25rem;color:#fff;padding-top: 0.04rem;font-size: 0.16rem;
}
.user_wrap .textpic .text span{height:0.16rem;line-height: 0.16rem;color:#fff;font-size: 0.16rem;display: block;
}
.user_wrap .textpic .text a{float:right;height:0.16rem;line-height: 0.16rem;color:#fff;margin-right:0.22rem ;font-size: 0.16rem;
}
.user_wrap .textpic .text2{width:78%;float:left;height:0.47rem;line-height: 0.47rem;
}
.user_wrap .textpic .text2 .texts{float:left;padding-right: 0.2rem;
}
.user_wrap .textpic .text2 span{float:left;display:block;color:#fff;padding-right: 0.04rem;font-size: 0.14rem;
}




