
.tep2_wrap{height:0.45rem;background:#eee;position: relative;width:100%;text-align: center;line-height: 0.45rem;border-bottom: 0.01rem solid #ccc;
}
.tep_wrap{height:0.45rem;background:#48C05A;position: relative;width:100%;text-align:left;padding-top: 0.1rem;
}
.tep_wrap .search{width:83%;height:0.3rem;border-radius: 0.05rem;border:none;text-indent: 2em;margin-left:0.1rem;color:#D2D3D2;display: block;
}
.tep_wrap .imgsearch{right:0.8rem;top:0.15rem
}
.tep2_wrap .return{left:0.2rem;top:0.1rem;width:0.3rem
}
.tep_wrap .imgcheat{right:0.2rem;top:0.15rem
}
.tep_wrap img{position: absolute;width:0.2rem;
}
.tep_wrap .imgfirst{left:0.15rem;top:0.15rem
}
.tep_wrap .imgsecond{right:0.6rem;top:0.15rem
}
.tep_wrap .imgthird{right:0.2rem;top:0.1rem;width:0.3rem
}
.tep2_wrap h3{font-size: 0.2rem;
}
/*头部样式*/



