
.doubantime_wrap{font-size: 0.13rem;border-radius: 0.1rem;margin-left: 0.2rem;margin-top: 0.1rem;
}
.doubantime_wrap .minWrap{width:80%;background:#fff;padding-bottom: 0.2rem;
}
.doubantime_wrap .up{font-size: 0.13rem;background:#ccc;padding-bottom: 0.3rem;text-align: center;border-radius: 0.1rem;
}
.doubantime_wrap .up p{font-size: 0.13rem;height:0.25rem;line-height: 0.25rem;color:#fdfaf9;text-align: left;padding-left: 0.05rem;
}
.doubantime_wrap .up h3{font-size: 0.16rem;height:0.26rem;line-height: 0.26rem;color:#fdfaf9;padding-top: 0.07rem;
}
.doubantime_wrap .up h4{font-size: 0.14rem;height:0.17rem;line-height: 0.17rem;color:#fdfaf9;
}
.doubantime_wrap .up span{color:#E7DBCE
}
.doubantime_wrap .mid{font-size: 0.13rem;padding:0 0.1rem
}
.doubantime_wrap .mid h3{font-size: 0.16rem;height:0.26rem;line-height: 0.26rem;color:#000;padding-top: 0.07rem;
}
.doubantime_wrap .mid span{font-size: 0.14rem;height:0.17rem;line-height: 0.17rem;color:#d3d3d3;overflow: hidden
}
.doubantime_wrap .mid .pictext{padding:0.1rem 0 0.1rem;border:none
}
.doubantime_wrap .mid .pictext .img{width:0.6rem;height:0.6rem;float:left;
}
.doubantime_wrap .mid .pictext .text{width:70%;float:left;padding-left: 0.1rem;
}
.doubantime_wrap .mid .pictext .text h4{font-size: 0.15rem;height:0.22rem;line-height: 0.22rem;color:#000;
}
.doubantime_wrap .mid .pictext .text p{font-size: 0.14rem;height:0.17rem;line-height: 0.17rem;color:#d3d3d3;overflow: hidden
}
.doubantime_wrap .mid .pictext .img img{width:100%;height:100%;border-radius: 50%;
}
.doubantime_wrap .mid span{font-size: 0.14rem;height:0.17rem;line-height: 0.17rem;color:#d3d3d3;text-align: left;display: block;
}
.doubantime_wrap .down{margin:0 0.1rem;height:0.47rem;border:1px solid #48BD5B;text-align: center;line-height: 0.47rem;color:#48BD5B;font-size: 0.16rem;border-radius: 0.06rem;
}
.doubantime_wrap{font-size: 0.13rem;
}
.doubantime_wrap{font-size: 0.13rem;
}






