
.find_wrap{padding:0 0.15rem;background:#fff
}
.find_wrap h3{display: inline-block;
}
.find_wrap .pic-text{position: relative;margin-top: 0.25rem;
}
.find_wrap .pic-text .imgpic{width:0.48rem;height:0.48rem;float:left;border-radius:0.02rem ;
}
.find_wrap .pic-text .text{width:1.68rem;float:left;padding-left: 0.1rem;
}
.find_wrap .pic-text .text h4{height:0.19rem;line-height: 0.19rem;font-size: 0.16rem;padding:0.02rem 0rem 0.01rem
}
.find_wrap .pic-text .text span{line-height: 0.12rem;font-size: 0.14rem;color: #BEBEBE;display: block;
}
.find_wrap .pic-text em{font-size: 0.12rem;color: #BEBEBE;font-style: normal;position: absolute;right:0.35rem;top:0.2rem
}
.find_wrap .pic-text .imgfor{width:0.23rem;height:0.23rem;position: absolute;right:0rem;top:0.12rem
}





