
.room_wrap{width:85%;background:#FFFFFF;margin: 0 auto;padding:0.18rem 0.1rem 0.2rem 0.15rem;border-radius: 0.1rem;position: relative;margin-bottom: 0.1rem;-webkit-box-shadow: 0rem 0rem 0.05rem rgba(0,0,0,0.2);box-shadow: 0rem 0rem 0.05rem rgba(0,0,0,0.2);
}
.room_wrap .up{height:0.47rem;
}
.room_wrap .up img{height:100%;float:left;border-radius: 50%;
}
.room_wrap .up .text{float:left;margin-top:0.03rem;padding-left: 0.12rem;
}
.room_wrap .up h3{font-size: 0.18rem;color:#3A3A3A
}
.room_wrap .up span{height:0.19rem;line-height: 0.19rem;font-size: 0.14rem;color:#B4B4B4;display: block;
}
.room_wrap .up a{height:0.27rem;line-height: 0.27rem;width:0.72rem;font-size: 0.16rem;color:#FFFFFF;background:#48C05A;position: absolute;display: block;right: 0.14rem;top:0.28rem;text-align: center;border-radius: 0.06rem;
}
.room_wrap .up .gray{background:#ccc
}
.room_wrap .down{padding:0rem 0.27rem 0rem 0.57rem;
}
.room_wrap .down .wenzi{height:0.35rem;line-height:0.35rem;font-size: 0.15rem;
}
.room_wrap .down p{line-height: 0rem.17rem;font-size: 0.14rem;background:#141B2C;padding:0rem 0.13rem;color:#C2C8D5
}
.room_wrap .down .pic{padding-top: 0.14rem;float:none;width:50%;overflow: hidden;
}
.room_wrap .down .pic .pics{float:left;width:0.45rem;height:0.24rem;
}
.room_wrap .down .pic img{width:0.24rem;float:left
}
.room_wrap .down .pic span{line-height: 0.24rem;float:left;width:0.18rem;overflow: hidden;font-size: 0.12rem;
}




