
.remind_wrap{padding-left:0.2rem;background:#fff;margin:0.15rem 0 0.2rem;
}
.remind_wrap .up{border-bottom: 0.01rem solid #E5E5E5;padding: 0.16rem 0;
}
.remind_wrap .up img{float:left;width:0.17rem;height:0.2rem;
}
.remind_wrap .up span{padding-left:0.13rem;float:left;height:0.2rem;line-height:0.2rem;font-size: 0.14rem;display: block;
}
.remind_wrap .up a{float:right;height:0.2rem;line-height:0.2rem;font-size: 0.16rem;display: block;padding-right: 0.23rem;
}
.remind_wrap .down{padding: 0.15rem 0 0.18rem;text-align: center;
}
.remind_wrap h4{font-size: 0.14rem;color:#D1D1D1
}



