
.footnav_wrap{height:0.47rem;width:100%;background:#FDFDFD;-webkit-box-shadow: 0.05rem 0 0.05rem rgba(0,0,0,0.5);box-shadow: 0.05rem 0 0.05rem rgba(0,0,0,0.5);position: fixed;bottom: 0.6rem;
