
*{margin: 0;padding: 0;
}
ul,li,span{list-style: none;
}
body{background:#F7F7F7
}
.clearfix:after{
				clear: both;
				display:block;
				visibility: hidden;
				height:0;
				content: "";
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding-bottom: 0.5rem;
}



